































import { State } from 'vuex-class';
import { Component, Vue } from 'vue-property-decorator';
import { ITicket } from '@/types/interface';
import banner from '../../components/Banner.vue';
import ticketItemPanel from './TicketItemPanel.vue';

@Component({
  components: {
    banner,
    ticketItemPanel,
  },
})

export default class MainPage extends Vue {
  @State('ticketList', { namespace: 'ticket' }) stateTicketList!: ITicket[] | [];

  link() {
    this.$router.go(-1);
  }

  flag = 'available';

  switchHandler(flag: string) {
    this.flag = flag;
  }

  get filterList() {
    return this.stateTicketList.filter((d: ITicket) => d.status === this.flag);
  }

  select(id: string) {
    if (id !== '') this.$router.push({ path: '/ticket/content', query: { id } });
  }
}
